<template>
  <nav>
    <div class="menu">
      <a href="/">
        <img src="./assets/img/logo.svg" alt="logo">
      </a>
      <div class="iconMenu">
          <i class="fa-solid fa-user-large" @click="[popupBlock() ,addIcon($event)]"></i>
          <i class="fa-solid fa-cart-shopping" @click="[cartMenu() ,addIcon($event)]"></i>
          <i class="fa-solid fa-heart" @click="[heartMenu() ,addIcon($event)]"></i>
      </div>
      <div class="textMenu">
        <router-link to="/">HOME</router-link>
        <router-link to="/product">PRODUCTS</router-link>
        <router-link to="/help">HELP</router-link>
      </div>
    </div>

    <!-- popup -->
    <div class="userPopup" v-bind:class="{'block': isBlock}">
      <div class="closeBox">
        <i class="fa-solid fa-x" v-on:click="popupNone"></i>
      </div>
      <div class="popupText">
        <i class="fa-solid fa-user-xmark"></i>
        <p>
          로그인이 필요한 서비스입니다.<br>
          로그인하시겠습니까?
        </p>
        <div class="answer">
          <button class="yes">YES</button>
          <button class="no" v-on:click="popupNone($event)">NO</button>
        </div>
      </div>
    </div>
  </nav>

  <div class="cartMenu">
    <i class="fa-solid fa-x" v-on:click="slideMenuNone($event)"></i>
    cartMenu
  </div>

  <div class="heartMenu">
    <i class="fa-solid fa-x" v-on:click="slideMenuNone"></i>
    heartMenu
  </div>
  <router-view />
</template>

<script>

  export default {
    data: function () {
      return {
        isBlock: false
      };
    },
    methods: {
      popupBlock: function(){
        this.isBlock = !this.isBlock;
      },
      popupNone: function(){
        var userIcon = document.querySelector(".fa-user-large");
        
        userIcon.classList.remove("clickIcon");
        this.isBlock = false;
      },
      addIcon: function(thisIcon){
        var target = thisIcon.target;
        target.classList.toggle("clickIcon");
      },
      slideMenuNone: function(xmark){
        var target = xmark.target;
        var menu = target.parentElement;
        var shopping = document.querySelector(".fa-cart-shopping");
        var heart = document.querySelector(".fa-heart");

        menu.style.transform = "translateX(110%)";
        shopping.classList.remove("clickIcon");
        heart.classList.remove("clickIcon");
      },
      cartMenu: function(){
        var cartMenu = document.querySelector(".cartMenu");
        cartMenu.style.transform = "translateX(0)"
      },
      heartMenu: function(){
        var heartMenu = document.querySelector(".heartMenu");
        heartMenu.style.transform = "translateX(0)"
      }
    }
  }

</script>

